@charset "UTF-8";

* {
  box-sizing: border-box;
}

body {
  width: 100%;
  margin: 0 auto;
}

ul {
  padding: 0;
  margin: 0;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
}

h1, h2, h3, h4, h5, p, article, figure, blockquote {
  margin: 0;
}

img {
  display: inline-block;
  max-width: 100%;
}
